.modal {
  display: flex;
  backdrop-filter: blur(5px);
  color: white;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}
.modal__container {
  max-width: 600px;
  position: absolute;
  top: 20.5%;
  left: 15%;
  padding: 10px;
  background: #12c2e9; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #f64f59,
    #c471ed,
    #12c2e9
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #f64f59,
    #c471ed,
    #12c2e9
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  height: 45%;
  width: 70%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
}
.modal__activity {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  font-weight: 400;
  margin: 15px 0;
  line-height: 15px;
}
.modal__title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  font-weight: 400;
  margin: 15px 0;
}
.modal__buttonContainer {
  display: flex;
  justify-content: space-between;
}
.modal__inputActivity {
  margin-left: 10px;
  font-family: $font-primary;
}
.desactive {
  display: none;
}
.modal__btnSubmit {
  background-color: #8bdb8e;
  border: none;
  padding: 5px 10px;
  &:active {
    transform: scale(0.9);
  }
}
.modal__buttonCancel {
  background-color: #ff4747;
  border: none;
  padding: 5px 10px;
  &:active {
    transform: scale(0.9);
  }
}
@include breakpoint(md) {
  .modal__form {
    width: 350px;
  }
  .modal__container {
    left: 20%;
  }
  .modal__title {
    font-size: 20px;
  }
  .modal__activity {
    font-size: 20px;
  }
}
@include breakpoint(lg) {
  .modal__container {
    left: 30%;
  }
  .modal__inputTitle {
    width: 200px;
    height: 25px;
  }
  .modal__inputActivity {
    width: 200px;
  }
}
